import React, { useEffect, useState, useCallback } from 'react';
import Info from './Info';
import Skills from './Skills';
import Projects from './Projects/index';
import Services from './Services';

const Main = () => {
  const [skillsVisible, setSkillsVisible] = useState(window.scrollY > 150);
  const [projectsVisible, setProjectsVisible] = useState(window.scrollY > 1000);
  const [servicesVisible, setServicesVisible] = useState(window.scrollY > 1880);

  const callback = useCallback(() => {
    if (window.scrollY > 150 && !skillsVisible) setSkillsVisible(true);
    if (window.scrollY > 1000 && !projectsVisible) setProjectsVisible(true);
    if (window.scrollY > 1880 && !servicesVisible) setServicesVisible(true);
  }, [skillsVisible, projectsVisible, servicesVisible]);

  useEffect(() => {
    window.addEventListener('scroll', callback);
  }, []);

  return (
    <section className="main">
      <Info />
      <Skills visible={skillsVisible} />
      <Projects visible={projectsVisible} />
      <Services visible={servicesVisible} />
    </section>
  );
};

export default Main;
