import React, { useEffect, useState } from 'react';

const services = [
  { title: 'Responsive design', image: 'responsive.svg' },
  { title: 'Fast delivery', image: 'time.svg' },
  { title: 'Optimized quality', image: 'optimize.svg' }
];

const Services = props => {
  const [visible, setVisible] = useState(props.visible);

  useEffect(() => {
    setVisible(props.visible);

    [...document.querySelectorAll('.service')].map((service, i) => {
      service.animate(
        [
          { transform: 'translateY(50%)', opacity: 0 },
          { transform: 'translateY(0)', opacity: 1 }
        ],
        { duration: 1000, delay: i * 500, fill: 'both' }
      );
    });
  }, [props.visible]);

  const renderServices = () => {
    return services.map((service, i) => (
      <div className="service" key={i}>
        <img
          src={require(`../../images/${service.image}`).default}
          alt="map icon"
          className="service__icon"
        />
        <h3 className="service__title">{service.title}</h3>
      </div>
    ));
  };

  return (
    <div className="services-section" id="services-section">
      <h3
        className={`primary-heading ${
          visible ? 'primary-heading-animate' : ''
        }`}>
        What do I offer?
      </h3>
      <div className="services">{renderServices()}</div>
    </div>
  );
};

export default Services;
