import React, { useState } from 'react';

const Navbar = () => {
  const [navOpened, setNavOpened] = useState(false);

  const toggleNav = () => {
    setNavOpened(prevValue => !prevValue);
  };

  const closeNav = () => {
    if (navOpened) setNavOpened(false);
  };

  return (
    <>
      <div
        className={`navigation__content ${
          navOpened ? 'navigation__content-opened' : ''
        }`}>
        <ul className="navigation__list">
          <a
            href="#skills-section"
            className="navigation__item"
            onClick={closeNav}>
            <li>
              <span className="navigation__opt"></span>Skills
            </li>
          </a>
          <a
            href="#projects-section"
            className="navigation__item"
            onClick={closeNav}>
            <li>
              <span className="navigation__opt"></span>Projects
            </li>
          </a>
          <a
            href="#services-section"
            className="navigation__item"
            onClick={closeNav}>
            <li>
              <span className="navigation__opt"></span>Services
            </li>
          </a>
          <a
            href="#contact-section"
            className="navigation__item"
            onClick={closeNav}>
            <li>
              <span className="navigation__opt"></span>Contact
            </li>
          </a>
        </ul>

        <div className="gear-icon gear-icon-1">
          <img src={require('../images/gear.svg').default} alt="gear icon" />
        </div>
        <div className="gear-icon gear-icon-2">
          <img src={require('../images/gear.svg').default} alt="gear icon" />
        </div>
      </div>

      <nav className="navigation">
        <img
          src={require('../images/wlogo-2.png').default}
          alt="logo"
          className="navigation__logo"
        />
        <ul className="navigation__opts">
          <span className="navigation__opt" onClick={toggleNav}></span>
          <span className="navigation__opt" onClick={toggleNav}></span>
          <span className="navigation__opt" onClick={toggleNav}></span>
        </ul>
      </nav>
    </>
  );
};

export default Navbar;
