import React, { useState } from 'react';
import { Player } from 'video-react';
import MobileModal from './MobileModal';

const projects = [
  {
    title: 'jobin',
    desc: `A job search application based on your own location or location you set`,
    poster: `jobin1.jpg`,
    video: `jobin.mp4`,
    cover: 'jobinCover.jpg'
  },
  {
    title: 'grocer',
    desc: `A shopping application design`,
    poster: 'grocer.jpg',
    video: `grocer.mp4`,
    cover: 'grocerCover.jpg'
  },
  {
    title: 'invoice',
    desc: `A finance application design`,
    poster: 'invoice.jpg',
    cover: 'invoiceCover.jpg'
  },
  {
    title: 'advance carousel',
    desc: `an advance carousel`,
    video: 'carousel-1.mp4',
    cover: 'advance carousel cover.jpg'
  },
  {
    title: 'advance carousel',
    desc: `an advance carousel`,
    video: 'carousel-2.mp4',
    cover: 'advance carousel cover.jpg'
  }
];

const MobileProjects = () => {
  const [showProject, setShowProject] = useState(null);

  const getProjectDetail = () => {
    return (
      <MobileModal>
        <div className="modal-content">
          <img
            src={require('../../../images/cancel.svg').default}
            onClick={() => setShowProject(null)}
            className="cancel-icon"
          />
          <div className="modal-project">
            <div className="modal-project__content">
              <h1 className="modal-project__title">{showProject.title}</h1>
              <p className="modal-project__desc">{showProject.desc}</p>
            </div>
            <div className="modal-project__media">
              {showProject.video ? (
                <Player
                  poster={
                    showProject.poster
                      ? require(`../../../images/mobile/${showProject.poster}`)
                          .default
                      : ''
                  }
                  src={
                    require(`../../../images/mobile/${showProject.video}`)
                      .default
                  }
                  muted
                  autoPlay
                  preload="auto"
                />
              ) : (
                <img
                  src={
                    require(`../../../images/mobile/${showProject.poster}`)
                      .default
                  }
                  className="modal-project__poster"
                />
              )}
            </div>
          </div>
        </div>
      </MobileModal>
    );
  };

  return (
    <>
      {showProject ? getProjectDetail() : null}
      {projects.map((project, i) => (
        <div key={i} className="projects__card">
          <img
            src={require(`../../../images/mobile/${project.cover}`).default}
            alt={project.title}
            className="projects__img"
          />
          <div className="projects__content">
            <h5 className="projects__title">{project.title}</h5>
            <p className="projects__desc">
              {project.desc}{' '}
              {project.underDevelopment ? (
                <>
                  <br />
                  under development
                </>
              ) : null}
            </p>
            <p className="projects__skills">React Native</p>
            <button
              className="projects__link"
              onClick={() => setShowProject(project)}>
              Open Project
            </button>
          </div>
        </div>
      ))}
    </>
  );
};

export default MobileProjects;
