import React, { useEffect, useState } from 'react';
import WebProjects from './WebProjects';
import MobileProjects from './MobileProjects';

const Projects = props => {
  const [visible, setVisible] = useState(props.visible);
  const [show, setShow] = useState('web');

  useEffect(() => {
    setVisible(props.visible);

    [...document.querySelectorAll('.projects__card')].map((card, i) => {
      card.animate(
        [
          { transform: 'translateY(50%)', opacity: 0 },
          { transform: 'translateY(0)', opacity: 1 }
        ],
        { duration: 1000, delay: i * 500, fill: 'both' }
      );
    });
  }, [props.visible]);

  return (
    <div className="projects-section" id="projects-section">
      <h2
        className={`primary-heading ${
          visible ? 'primary-heading-animate' : ''
        }`}>
        what have i done?
      </h2>
      <div style={{ marginBottom: '20px' }}>
        <button
          className="toggle__btn"
          style={{ backgroundColor: show === 'web' ? 'orange' : 'black' }}
          onClick={() => setShow('web')}>
          web
        </button>
        <button
          className="toggle__btn"
          style={{ backgroundColor: show === 'mobile' ? 'orange' : 'black' }}
          onClick={() => setShow('mobile')}>
          mobile
        </button>
      </div>
      <div className="projects">
        {show === 'web' ? (
          <WebProjects visible={visible} />
        ) : (
          <MobileProjects />
        )}
      </div>
    </div>
  );
};

export default Projects;
